*{
    box-sizing: border-box;
    margin: 0;
}

html{
    font-size: 8px;
}

body{
    overflow-x: hidden;
}

/* 
Add variables(if any) here
*/

/* For layout - start*/

.club-title{
    position: absolute;
    width: 100%;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
}

.club-main-block-image{
    height: 100vh;
}

.club-main-block, .club-block{
    width: 100%;
    display: flex;
    flex-direction: column;
}

.club-block-text{
    height: 70vh;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 30px;
}


.club-block-image, .club-main-block-image{
    height: 100vh;
    width: 100%;
}

.club-image{
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
}

.club-block-content{
    margin-top: 12px;
}

/*For layout - end*/



/* colorsand styles -start */

.club-title{
    font-size: 3rem;
}

.club-for-the-effect{
    margin: 0 auto;
    position: relative;
    padding: 30% 10px;
    background: rgba(255, 255, 255, 0.3);
    -webkit-backdrop-filter: blur(0px);
    backdrop-filter: blur(4px); 
}

.club-for-the-effect{
    display: inline-block;
}

.club-block{
    background-color: #494848;
    color: #FFFFFF;
}
.club-block:nth-of-type(2n){
    /* This selctor is for providing contrast colors for alternating blocks */
    background-color: #B4B4B4;;
    color: #494848;
}

.club-block-header{
    font-size: 2em;
}

.club-block-content{
    font-size: 1.3em;
}

.club-title-first-half{
    color: blanchedalmond;
}

.club-title-second-half{
    color: #333;
}

.club-text-wrapper{
    width: 60%;
}
/* colors and styles -end */

@media screen and (min-width: 230px) {
    html{
        font-size: 8px;
    }
}

@media screen and (min-width : 300px){
    html{
        font-size: 11px;
    }
}

@media screen and (min-width : 450px){
    html{
        font-size: 16px;
    }
}

@media screen and (min-width:768px) {

    html{
        font-size: 13px;
    }
    
    .club-title{
        background-color: #B4B4B4;
        width: 40%;
        position: static;
    }

    .club-main-block-image{
        width: 60%;
    }

   .club-main-block, .club-block{
        flex-direction: row;
    }

    .club-block:nth-of-type(2n){
        flex-direction: row-reverse;
    }

    .club-block-image, .club-block-text{
        width: 50%;
        height: 80vh;
    }

    .club-for-the-effect{
        left: 50%;
    }

    body::-webkit-scrollbar{
        width: 3px;
    }

    body::-webkit-scrollbar-track{
        background-color: #B4B4B4;
    }

    body::-webkit-scrollbar-thumb{
        background-color: #494848;
    }

    
}

@media screen and (min-width:900px) {
    html{
        font-size: 14px;
    }
}

@media screen and (min-width:1150px) {

    html{
        font-size: 14px;
    }

    .club-title{
        width: 30%;
    }

    .club-main-block-image{
        width: 70%;
    }

    .club-block-text{
        width: 55%;
    }
    .club-block-image{
        width: 45%;
    }

    .club-for-the-effect{
        font-size: 3rem;
    }
}

@media screen and (min-width:1400px){
    html{
        font-size: 16px;
    }
}

